import React from 'react';
import PropTypes from 'prop-types';
import {Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, View, Tabbar, TabbarItem, Epic, Card} from '@vkontakte/vkui';
import connect from "@vkontakte/vkui-connect";
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon28Messages from '@vkontakte/icons/dist/28/messages';
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';
import Icon28More from '@vkontakte/icons/dist/28/more';

import Persik from './Persik';
import ReactDOM from "react-dom";

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Example</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Connect">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		<Group title="Navigation Example">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Show me the Persik, please
				</Button>
			</Div>
		</Group>
	</Panel>
);
class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			activeStory: 'feed',
		};
	}

	componentDidMount() {

		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}
	onStoryChange =(e)=>{
		this.setState({ activeStory: e.currentTarget.dataset.story })
	}
	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	render() {
		const { id, go, fetchedUser } =this.props;
		return (

                <Epic activeStory={this.state.activeStory} tabbar={
                    <Tabbar>
                        <TabbarItem
                            onClick={this.onStoryChange}
                            selected={this.state.activeStory === 'feed'}
                            data-story="feed"
                            text="События"
							label="2"
                        ><Icon28Newsfeed /></TabbarItem>
                        <TabbarItem
                            onClick={this.onStoryChange}
                            selected={this.state.activeStory === 'discover'}
                            data-story="discover"
                            text="Поиск"
                        ><Icon28Search /></TabbarItem>
                        <TabbarItem
                            onClick={this.onStoryChange}
                            selected={this.state.activeStory === 'messages'}
                            data-story="messages"
                            text="Сообщения"
                        ><Icon28Messages /></TabbarItem>
                        <TabbarItem
                            onClick={this.onStoryChange}
                            selected={this.state.activeStory === 'notifications'}
                            data-story="notifications"
                            text="Уведомлен."
                        ><Icon28Notifications /></TabbarItem>
                        <TabbarItem
                            onClick={this.onStoryChange}
                            selected={this.state.activeStory === 'more'}
                            data-story="more"
                            text="Ещё"
                        ><Icon28More /></TabbarItem>
                    </Tabbar>
                }>
                    <View id="feed" activePanel="feed">
                        <Panel id="feed">
                            <PanelHeader>Events</PanelHeader>
							<Persik />
                        </Panel>
                    </View>
                    <View id="discover" activePanel="discover">
                        <Panel id="discover">
                            <PanelHeader>Discover</PanelHeader>
                        </Panel>
                    </View>
                    <View id="messages" activePanel="messages">
                        <Panel id="messages">
                            <PanelHeader>Messages</PanelHeader>
                        </Panel>
                    </View>
                    <View id="notifications" activePanel="notifications">
                        <Panel id="notifications">
                            <PanelHeader>Notifications</PanelHeader>
                        </Panel>
                    </View>
                    <View id="more" activePanel="more">
                        <Panel id="more">
                            <PanelHeader>More</PanelHeader>
                        </Panel>
                    </View>
                </Epic>

		);
	}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default App;
